<template>
  <header class="navbar">
    <Row>
      <Col span="24">
        <Form>
          <Form-item>
            <h5>{{textDic.title}}</h5>
          </Form-item>
          <Form-item>
            <div style="width: 1200px; height: 1px; background-color: #333;margin: 20px 0;"></div>
          </Form-item>
        </Form>
      </Col>
    </Row>
    <Row>
      <Col span="24">
      <header class="navbar">
        <span style="margin-right: 50px;">{{textDic.text0}}</span>
        <Input v-model="searchName" :placeholder="textDic.text0" style="width: 200px; margin-right: 50px;"/>
        <span style="margin-right: 50px;">{{textDic.text1}}</span>
        <Input v-model="searchName" :placeholder="textDic.text1" style="width: 200px; margin-right: 50px;"/>
        <Button type="primary" style="margin-right: 10px;">{{textDic.text2}}</Button>
        <Button type="primary">{{textDic.text3}}</Button>
      </header>
      </Col>
    </Row>
    <Row>
      <Col span="24">
        <header class="navbar addBtn">
          <Button type="primary" @click="didClickAdd">{{textDic.text4}}</Button>
        </header>
        <table border="1px" cellspacing="0" cellpadding="0">
          <tr>
            <th v-for="(title, i) in textDic.tableTitles" :key="i">{{title}}</th>
          </tr>
          <tr v-for="(formItem, index) in formItems" :key="index">
            <td v-for="(text, j) in formItem" :key="j">
              <custom-cell :index="index" :cellData="cellText(j, text)" :type="cellType(j)" @didClickButton="didClickButton"></custom-cell>
            </td>
          </tr>
        </table>
    <Modal
        v-model="modal"
        :title="textDic.text4"
        @on-ok="didClickModel(true)"
        @on-cancel="didClickModel(false)">
        <Form :label-width="80">
          <Form-item v-for="(text, index) in textDic.editTitles" :label="text" :key="index">
            <Input v-if="index != textDic.editTitles.length-1" v-model="editForm[index]"/>
            <!-- <select v-if="index == textDic.editTitles.length-1" v-model="editForm[index]">
              <option v-for="(t, i) in " :value="textDic.text6">{{textDic.text6}}</option>
              <option :value="textDic.text7">{{textDic.text7}}</option>
            </select> -->
            <Select v-if="index == textDic.editTitles.length-1" v-model="editForm[index]" filterable>
                <Option v-for="(item, i) in auPeriod" :value="item" :key="i">{{ item }}</Option>
            </Select>
          </Form-item>
        </Form>
    </Modal>
      </Col>
    </Row>
    </header>
</template>

<script>
import CustomCell from '../components/CustomCell'
export default {
  components: {
    CustomCell
  },

  data() {
    return {
      formItems: [
        [1546247235000, '上海', '13444444445', 'AW2312', '独孤求败', '13333333333', '房源', 1546247235000, '过期', '周杰伦', 1546247235000, ''],
        [1546247235000, '苏州', '13444444445', 'JE2345', '任我行', '13333333333', '房源、发布', 1546247235000, '正常', '林俊杰', 1546247235000, ''],
        [1546247235000, '南京', '13444444445', '0000', '令狐冲', '13333333333', '', 1546247235000, '未开通', '王力宏', 1546247235000, ''],
      ],
      editForm: ['', '', '', '', ''],
      currentIndex: 0,
      modal: false, 
      auPeriod: ['请选择', '一个月', '三个月', '六个月', '一年', '自定义'], // 授权期限
    }
  },

  methods: {
    cellText(j, text) {
      return j == this.textDic.tableTitles.length - 1 ? [this.textDic.text5] : [text]
    },
    cellType(j) {
      if(j == 0 || j == 7) {
        return 'date';
      }else if(j == this.textDic.tableTitles.length - 1) {
        return 'button';
      }else {
        return 'text';
      }
    },
    didClickButton(data) {
      this.currentIndex = data.index;
      this.modal = true;
    },
    didClickAdd() {

    },
    didClickModel(isEdit) {

    }
  },
  computed:{
    textDic() {
      return {
        title: '用户信息',
        tableTitles: ['注册时间', '区域', '手机号', '推荐码', '推荐人', '邀请人手机号', '已购买模块', '过期日期', '状态', '操作人', '操作时间', '操作'],
        editTitles: ['手机号', '区域', '推荐人', '推荐码', '授权期限'],
        text0: '姓名',
        text1: '手机号',
        text2: '搜索',
        text3: '重置',
        text4: '添加',
        text5: '模块授权'
      }
    }
  }

}
</script>

<style>
.addBtn {
    background-color: #333;
    margin-top: 60px;
    padding: 5px 10px;;
  }
  td, th {
    width: 218px; 
    height: 60px;
  }
</style>




